import React, { Component } from 'react';
import { NavLink } from "react-router-dom"
import ggggg from '../img/5g.png'
import VR from '../img/vr.png'
import BC from '../img/blockchain.png'
import iot from "../img/iot.png"

class About extends Component {
  render() {
    return (
      <div className="main-container">           
            <div className='index-container'>
              <div className="col-lg-12 text-justify yahei">
                <h1></h1>
                <ol className="breadcrumb bg-transparent p-1">
                  <li className="breadcrumb-item"><NavLink to="/"></NavLink></li>
                  <li className="breadcrumb-item active"></li>
                </ol>
                <hr/>
              </div>
              <div className="mx-3">
                <div className="mb-5 text-justify">
                  <div className="md-4">
                    <h4 className='yahei'></h4>
                    <p>&emsp;&emsp;
                      VR/AR  5G/6G 
                      </p>
                  </div>
                  <div className="md-4">
                    <h4 className='yahei'></h4>
                    <div className='d-flex row'>
                      <div className='col-lg-3 pb-3 text-center'>
                        <NavLink to='/research/ec'>
                          <div className='backgroundcircle m-auto'>
                            <img src={ggggg} alt='icon'/>
                          </div>
                          <div className='pt-3'>
                            <p className='yahei'>5G</p>
                            <div style={{fontSize:"0.9rem"}}>
                              <span></span>
                              <br/>
                              <span></span>
                              <br/>
                            </div>
                          </div>
                        </NavLink>
                      </div> 
                      <div className='col-lg-3 pb-3 text-center'>
                        <NavLink to='/research/vr'>
                          <div className='backgroundcircle m-auto'>
                            <img src={VR} alt='icon'/>
                          </div>
                          <div className='pt-3'>
                            <p className='yahei'></p>
                            <div style={{fontSize:"0.9rem"}}>
                              <span>B5G/6G</span>
                              <br/>
                            </div>
                          </div>
                        </NavLink>
                      </div>
                      <div className='col-lg-3 pb-3 text-center'>
                        <NavLink to='/research/bc'>
                          <div className='backgroundcircle m-auto'>
                            <img src={BC} alt='icon'/>
                          </div>
                          <div className='pt-3'>
                            <p className='yahei'></p>
                            <div style={{fontSize:"0.9rem"}}>
                              <span></span>
                              <br/>
                              <span></span>
                              <br/>
                              <span> NB-IoT SIM/eSIM </span>
                              <br/>
                              <span></span>
                              <br/>
                            </div>
                          </div>
                        </NavLink>
                      </div>
                     
                      <div className='col-lg-3 pb-3 text-center'>
                        <NavLink to='/research/bc'>
                          <div className='backgroundcircle m-auto'>
                            <img src={iot} alt='icon'/>
                          </div>
                          <div className='pt-3'>
                            <p className='yahei'></p>
                            <div style={{fontSize:"0.9rem"}}>
                              <span></span>
                              <br/>
                            </div>
                          </div>
                        </NavLink>
                      </div>                      
                    </div>
                  </div>                                    
                </div>
              </div>  
            </div>          
        </div>
    );
  }
}
export default About;