import React, { Component } from 'react';
import { NavLink } from "react-router-dom"

class Research extends Component {
    render() {
        return (
            <div className="main-container">
                <div className='index-container'>
                    <div className="col-lg-12 text-justify yahei">
                       <h1></h1>
                        <ol className="breadcrumb bg-transparent p-1">
                            <li className="breadcrumb-item"><NavLink to="/"></NavLink></li>
                            <li className="breadcrumb-item active"></li>
                        </ol>                        
                        <hr />
                    </div>
                    <div className="mx-3">
                        <div className="mb-5 text-justify">
                            <div className="mb-5">
                                <p></p>
                                <ul>
                                    <li className='my_link'><NavLink to="/research/bc"><span>/</span></NavLink></li>
                                    <li className="my_link"><NavLink to="/research/vr"><span>VR/AR</span></NavLink></li>
                                    <li className='my_link'><NavLink to="/research/ec"><span>5G</span></NavLink></li>
                                </ul>
                                <p style={{fontSize:'0.9rem'}}></p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
export default Research;