import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';

class Home extends Component {
  render() {
    return (
      <div className="main-container">
            <img alt='bgimg' src={require('../img/bg1.png')} style={{width:"100%" ,maxHeight:"400px"}}/>       
            <div className='index-container'> 
              <div className="mb-5 row">                
                <div className="col-lg container">
                  <h2 className="yahei"><i className="fas fa-home"></i> </h2>
                  <p className='text-left'>&emsp;&emsp;
                      VR/AR  5G/6G </p>                  
                </div>
                <div className="col-lg container">
                  <h2 className="yahei"><i className="fas fa-user-friends"></i> </h2>
                  <ul className="text-left">
                  	<li>5G edge computing</li>
                  	<li>VR/AR :</li>
                    <li>AIoT</li>
                  	<li></li>
                  </ul>
                  <NavLink to="/about"><button className="btn btn-outline-primary">more...</button></NavLink>
                </div>
              </div>
            </div>
        </div>
    );
  }
}
export default Home;